<template>
  <v-container fluid grid-list-sm>
    <v-layout
      ref="card"
      xs12
      style="background-color: var(--v-cardBackground-base);--v-image-actual-height: 70px"
      justify-space-between
      ma-0 pa-0
    >
      <v-flex
        shrink
        style="width: var(--v-theme-image-width);max-width: 40%"
        mt-0
        pt-0
        mb-0
        pb-0
        v-if="hasImage || playable"
        v-bind:class="imageClassObject"
      >
        <v-img
          ref="itemImage"
          :aspect-ratio="1/1"
          :key="item.guid + 'image'"
          class="image ma-0 mb-2 pa-0 text-center"
          :src="imageSrc"
          @error="onError"
          style="width: var(--v-theme-image-width);"
          v-on="!playable ? { click: itemClicked } : {}"
        >
          <v-container ma-0 pa-0 fluid fill-height align-end>
            <v-row ma-0 pa-0 no-gutters>
              <v-col ma-0 pa-0>
                <vue-resize-sensor @resize="onImageResize" style="position:absolute;top:0;bottom:0;left:0;right:0" />
                <PlayButtonSquare
                  class="pa-0 ma-0"
                  v-if="playable"
                  :item="item"
                  large
                  :playerColor="hasImage ? 'white' : 'black'"
                  showMediaPlayer
                  v-on:playStarted="onPlayStarted($event)"
                />
              </v-col>
            </v-row>
          </v-container>
        </v-img>
      </v-flex>

      <v-flex @click="itemClicked()" v-bind:class="textClassObject" mt-0 pt-0 mb-0 pb-0 
      style="display: flex; flex-direction: column;max-height: var(--v-image-actual-height);overflow:auto">
        <div class="pa-0" style="flex:1 1 auto;display: flex; flex-direction: column;overflow:auto">
          <div style="flex: 0 0 auto" ref="itemCategory" class="itemCategory" v-if="item.category != null && item.category.length > 0">{{ item.category }}</div>
          <div style="flex: 1 1 auto;display:flex">
            <vue-resize-sensor style="flex: 1 1 auto" @resize="onTitleResize">
              <div style="position:absolute;top:0;left:0;right:0;bottom:0" ref="itemTitle" class="itemTitle itemTitle4lines">{{ item.title }}</div>
            </vue-resize-sensor>
          </div>
          <div style="flex: 0 0 auto" />
          <div style="flex: 0 0 auto;line-height:15px" ref="itemDate" class="nobreak pa-0 ma-0">
            <DateView class="itemDate" :date="item.pubDate" ago />&nbsp;
            <ItemType :item="item" />
          </div>
        </div>
      </v-flex>

      <v-flex order-xs3 v-if="showFavorites">
        <ItemFavoriteButton :item="item" />
      </v-flex>
    </v-layout>
  </v-container>
</template>


<script>
import ItemBase from "./ItemBase";
import ItemType from "./ItemType";
import ItemFavoriteButton from "./ItemFavoriteButton";
import ItemModel from "../models/itemmodel";
import DateView from "./DateView";
import PlayButtonSquare from "./PlayButtonSquare";

export default {
  extends: ItemBase,
  components: {
    DateView,
    PlayButtonSquare,
    ItemType,
    ItemFavoriteButton
  },
  props: {
    item: {
      type: ItemModel,
      default: function() {
        return new ItemModel();
      }
    },
    showFavorites: {
      type: Boolean,
      default: function() {
        return false;
      }
    }
  },
  // mounted() {
  //   console.log("Item mounted: " + this.item.title);
  // },
  methods: {
    onError(ignoredE) {},
    onImageResize(ignoredElement) {
      const image = this.$refs.itemImage;
      if (image) {
          this.$refs.card.style.setProperty("--v-image-actual-height", (image.$el.clientHeight + 4) + "px");
      }
    },
    onTitleResize(size) {
      // We figure out how much space we have, then set max-height to an even number of lines!
      //
      // 1. Get height of container
      const itemTitle = this.$refs.itemTitle;
      const h = size.h;
      // 2. Get line height and calculate an integer number of lines we can display
      var lineHeight = parseFloat(getComputedStyle(itemTitle).getPropertyValue("line-height"));
      var numLines = Math.floor(h / lineHeight);
      // 3. Set max-height to this value
      itemTitle.style.setProperty("max-height", (numLines * lineHeight) + "px");
    }
  },
  computed: {
    imageClassObject: function() {
      var sort = 1;
      if (this.odd) {
        sort = 2;
      }
      let sortClass = "order-xs" + sort;
      let o = {};
      o[sortClass] = true;
      return o;
    },
    textClassObject: function() {
      var sort = this.hasImage || this.playable ? 2 : 1;
      let o = {};
      if (this.odd) {
        sort = 1;
        o["ml-0"] = true;
        o["mr-2"] = true;
      } else {
        o["mr-0"] = true;
        o["ml-2"] = true;
      }
      let sortClass = "order-xs" + sort;
      o[sortClass] = true;
      return o;
    }
  }
};
</script>

<style scoped>
.contentBlock {
  display: block;
}

.imageContainer {
  position: relative;
  width: 100%;
  padding-top: 76%;
}

.imageContainerContent {
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.image {
  object-fit: cover;
  background-color: #efefef;
  border-radius: 6px;
}

.nobreak {
  overflow: hidden;
  white-space: nowrap;
  padding: 0;
}
</style>
